import { TEXTURES } from "./Constants/Textures"
import { CONSTANTS } from "./Constants/Constants"
const costToLocation = (from, to) => {
  let dx = to[0] - from[0]
  let dy = to[1] - from[1]
  let distance = Math.sqrt(dx * dx + dy * dy)
  return distance * 100
}
const getActionType = action => {
  return action[0] + action[1] === 0 || Math.abs(action[0] + action[1]) === 2
    ? "diagonal"
    : "cardinal"
}
const isLegalAction = (x, y, action, state) => {
  const destinationPos = { x: x + action[0], y: y + action[1] }
  const actionType = getActionType(action)
  if (isOutOfBoundaries(destinationPos, state.gridWidth, state.gridHeight)) return false
  const isDestinationWall =
    state.texturesMap[destinationPos.y * state.gridWidth + destinationPos.x] === TEXTURES.WALL
  if (actionType === "diagonal") {
    const isHorizontalWall =
      state.texturesMap[y * state.gridWidth + destinationPos.x] === TEXTURES.WALL
    const isVerticalWall =
      state.texturesMap[destinationPos.y * state.gridWidth + x] === TEXTURES.WALL

    return !(isHorizontalWall || isVerticalWall || isDestinationWall)
  } else {
    return !isDestinationWall
  }
}
const addToOpen = (nodeToAdd, open) => {
  let added = false
  // check if its already in the open list.
  for (let index = 0; index < open.length; index++) {
    let node = open[index]
    if (
      nodeToAdd.x === node.x &&
      nodeToAdd.y === node.y &&
      nodeToAdd.g >= node.g &&
      (nodeToAdd.health === null || nodeToAdd.health <= node.health)
    ) {
      added = true
      break
    }
  }
  // if its not already in the open list add where it belongs based on the f value.
  if (!added) {
    const nodeToAddF = nodeToAdd.g + nodeToAdd.h
    // keep going until the f value is more than or qrual to the one in the open list and that where you add the node.
    for (let index = 0; index < open.length; index++) {
      const node = open[index]
      const nodeF = node.g + node.h
      if (nodeToAddF >= nodeF) {
        added = true
        open.splice(index, 0, nodeToAdd)
        break
      }
    }
  }
  // if was not added because it the smallest f then add it to the end..
  if (!added) {
    open.push(nodeToAdd)
  }
}

const getPath = node => {
  let action = node.action
  let parent = node.parent
  while (parent !== null && parent.action !== null) {
    action = parent.action
    parent = parent.parent
  }

  return action
}
const isOutOfBoundaries = (position, gridWidth, gridHeight) => {
  return position.x < 0 || position.y < 0 || position.x >= gridWidth || position.y >= gridHeight
}
const leadsToDeadEnd = (initialLocation, actionToLocation, actions, state) => {
  let newLocation = [
    initialLocation[0] + actionToLocation[0],
    initialLocation[1] + actionToLocation[1]
  ]
  let open = []
  let closed = new Array(state.gridWidth * state.gridHeight).fill(false)

  open.push([newLocation, actionToLocation])
  while (true) {
    if (open.length === 0) {
      return true
    }
    let node = open.pop()
    let latestAction = node[1]
    let location = node[0]
    if (closed[location[1] * state.gridWidth + location[0]]) continue
    closed[location[1] * state.gridWidth + location[0]] = true
    for (let i = 0; i < actions.length; i++) {
      let action = actions[i]
      if (action[0] + latestAction[0] === 0 && action[1] + latestAction[1] === 0) continue
      if (location[0] === initialLocation[0] && location[1] === initialLocation[1]) {
        return false
      }
      if (isLegalAction(location[0], location[1], action, state)) {
        newLocation = [location[0] + action[0], location[1] + action[1]]
        open.push([newLocation, action])
      }
    }
  }
}
const getFurthestLocationFrom = (location, actions, state) => {
  let open = []
  open.push({ location: location, cost: 0 })
  let closed = new Array(state.gridWidth * state.gridHeight).fill(false)
  let costs = new Array(state.gridWidth * state.gridHeight).fill(0)
  while (true) {
    if (open.length === 0) {
      let index = costs.indexOf(Math.max(...costs))
      let furthestLocation = [index % state.gridWidth, Math.floor(index / state.gridWidth)]
      return furthestLocation
    }
    let node = open.shift()
    let location = node.location
    let costSoFar = node.cost
    let index = location[1] * state.gridWidth + location[0]
    if (closed[index]) continue
    closed[index] = true
    for (let i = 0; i < actions.length; i++) {
      let action = actions[i]
      if (isLegalAction(location[0], location[1], action, state)) {
        let newLocation = [location[0] + action[0], location[1] + action[1]]
        let costOfLocation = costSoFar + 100
        // be carful when adding lava there is a problem when there is qual distance to path, and one has lava and the other does not, it will just take the one with the more lava.
        // if (state.texturesMap[newLocation[1] * state.gridWidth + newLocation[0]] === TEXTURES.LAVA)
        //   costOfLocation += 100
        if (closed[newLocation[1] * state.gridWidth + newLocation[0]]) continue
        open.push({ location: newLocation, cost: costOfLocation })
        costs[newLocation[1] * state.gridWidth + newLocation[0]] = costOfLocation
      }
    }
  }
}
const playerIsInPath = (node, state, actions) => {
  let pathLocations = []
  pathLocations.push([node.x, node.y])
  let parent = node.parent
  while (parent !== null) {
    pathLocations.push([parent.x, parent.y])
    parent = parent.parent
  }
  for (let i = 0; i < pathLocations.length; i++) {
    let location = pathLocations[i]
    let xDistance = Math.abs(location[0] - state.currentPlayerLocation[0])
    let yDistance = Math.abs(location[1] - state.currentPlayerLocation[1])
    if ((xDistance === 1 && yDistance === 0) || (xDistance === 0 && yDistance === 1)) {
      return true
    }
  }
  return false
}
const getNextThiefAction = (actions, state) => {
  let start = state.currentThiefLocation
  let goal = getFurthestLocationFrom(state.currentPlayerLocation, actions, state)
  let closed = new Array(state.gridWidth * state.gridHeight).fill(false)
  if (goal[0] === start[0] && goal[1] === start[1]) return [0, 0]
  let open = []
  open.push(new Node(start[0], start[1], null, null, null, 0, costToLocation(start, goal)))
  while (true) {
    if (open.length === 0) {
      return [0, 0]
    }

    let node = open.pop()
    if (node.x === goal[0] && node.y === goal[1]) {
      return getPath(node)
    }

    // checks if node in closed, where closed is an array with true false values for any state at x,y by accessing index at y * the with + x
    if (closed[node.y * state.gridWidth + node.x]) {
      continue
    }

    closed[node.y * state.gridWidth + node.x] = true
    actions.forEach((action, index) => {
      if (isLegalAction(node.x, node.y, action, state)) {
        const newLocation = { x: node.x + action[0], y: node.y + action[1] }
        let g = node.g + 100
        const h = costToLocation([newLocation.x, newLocation.y], goal)
        const newNode = new Node(newLocation.x, newLocation.y, null, node, action, g, h)
        addToOpen(newNode, open)
      }
    })
  }
}
const getNextAction = (state, props, characterType) => {
  let actions = [
    [0, 1],
    [0, -1],
    [1, 0],
    [-1, 0]
  ]
  let diagonalActions = [
    [1, 1],
    [-1, 1],
    [1, -1],
    [-1, -1]
  ]
  if (props.allowDiagonalActions) actions = actions.concat(diagonalActions)
  if (characterType === CONSTANTS.THIEF && props.followCursor) return [0, 0]
  if (characterType === CONSTANTS.THIEF) return getNextThiefAction(actions, state)
  let start = state.currentPlayerLocation
  let goal = state.currentThiefLocation
  let foundPath = false
  let bestPathNode = null
  let bestPathFoundHealth = 0
  //edge case when start is the goal.
  if (start[0] === goal[0] && start[1] === goal[1]) {
    return [0, 0]
  }

  let actionsCost = [100, 100, 100, 100, 144, 141, 141, 141]
  let closed = [...new Array(state.gridWidth * state.gridHeight)].map(() =>
    new Array(props.playerMaxHealth).fill(false)
  )

  let open = []
  open.push(
    new Node(
      start[0],
      start[1],
      state.currentPlayerHealth,
      null,
      null,
      0,
      costToLocation(start, goal)
    )
  )
  while (true) {
    if (open.length === 0) {
      if (foundPath) {
        return getPath(bestPathNode)
      }
      return [0, 0]
    }

    let node = open.pop()
    if (node.x === goal[0] && node.y === goal[1]) {
      if (node.health === props.playerMaxHealth || props.searchPriority === CONSTANTS.SPEED) {
        return getPath(node)
      } else if (node.health > bestPathFoundHealth) {
        foundPath = true
        bestPathFoundHealth = node.health
        bestPathNode = node
      }
    }

    // checks if node in closed, where closed is an array with true false values for any state at x,y by accessing index at y * the with + x
    if (closed[node.y * state.gridWidth + node.x][node.health - 1]) {
      continue
    }

    closed[node.y * state.gridWidth + node.x][node.health - 1] = true
    if (node.health <= 0) continue
    actions.forEach((action, index) => {
      if (isLegalAction(node.x, node.y, action, state)) {
        const newLocation = { x: node.x + action[0], y: node.y + action[1] }
        let g = node.g + actionsCost[index]
        const h = costToLocation([newLocation.x, newLocation.y], goal)
        const healthPackOnLocation =
          state.overLayMap[newLocation.y * state.gridWidth + newLocation.x] === TEXTURES.HEALTH_PACK
        const isLava =
          state.texturesMap[newLocation.y * state.gridWidth + newLocation.x] === TEXTURES.LAVA

        const health = healthPackOnLocation
          ? props.playerMaxHealth
          : isLava
          ? node.health - 1
          : node.health
        if (isLava && !healthPackOnLocation) g += 20

        const newNode = new Node(newLocation.x, newLocation.y, health, node, action, g, h)
        addToOpen(newNode, open)
      }
    })
  }
}

class Node {
  constructor(x, y, health, parent, action, g, h) {
    this.x = x
    this.y = y
    this.health = health
    this.action = action
    this.parent = parent
    this.g = g
    this.h = h
  }
}
export default getNextAction
